:root {
    --blues: #001affee;
    --whites: white;
    --bg-color-head: purple;
}

header {
    display: grid;
    grid-template-columns: 2fr 1fr;
    background-color: var(--bg-color-head);
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
}

nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: end;
    align-items: center;
}

.logo {
    color: var(--whites);
}

.nav-item {
    color: var(--whites);
    font-size: 30px;
    font-weight: bold;
    margin-left: 50px;
    cursor: pointer;
    text-decoration: none;
}

.nav-item:hover {
    color: var(--whites);
}

.count-sector{
    display: grid;
    grid-template-columns: 2fr 0.5fr;
}

.count-basket-items{
    line-height: 0px;
    border-radius: 25%;
    background: var(--whites);

    font-size: x-large;
    font-weight: bold;
    color:var(--blues);

    height: 25px;
    width: 25px;
    margin: auto;
    margin-left: 5px;
}

.count-basket-items span {
    display: inline-block;
    padding-top: 50%;
    padding-bottom: 50%;
    margin-left: 8px;
    margin-right: 8px;
}

.count-favorites-items{
    line-height: 0px;
    border-radius: 25%;
    background-color: var(--whites);

    font-size: x-large;
    font-weight: bold;
    color:var(--blues);

    height: 25px;
    width: 25px;
    margin: auto;
    margin-left: 5px;
}

.count-favorites-items span {
    display: inline-block;

    color: var(--blues);
    position: relative;

    padding-top: 50%;
    padding-bottom: 50%;
    margin-left: 8px;
    margin-right: 8px;
}

