:root {
    --whites: white;
    --bg-color-foo: purple;
}

footer {
    display: grid;
    grid-template-columns: 1fr 2fr;
    background-color: var(--bg-color-foo);
    padding: 30px;
    justify-items: center;
    align-items: center;
    color: var(--whites);

    margin-top: 200px;
}

footer .logo {
    font-size: 45px;
    font-weight: bold;
}