:root {
    --blues: #001affee;
    --greys_high: #616161;
    --greys_mid: #9f9f9ff1;
    --grays_low: #aca9a9;
    --whites: white;
}

.product-item {
    display: grid;
    border: 1px solid var(--greys_mid);
    border-radius: 35px;
    padding: 20px;
    margin: 30px;
    transition: 0.4s;
}

.product-item:hover {
    box-shadow: 0px 0px 10px 0px var(--greys_high);
}

.favorite-btn {
    border: 2px solid var(--blues);
    border-radius: 10px;
    background-color: var(--whites);
    color: var(--blues);
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    transition: 0.5s;
    cursor: pointer;
}

.favorite-btn-added {
    border: 0;
    border-radius: 10px;
    background-color: var(--blues);
    color: var(--whites);
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    transition: 0.5s;
    cursor: pointer;
}

.product-img {
    justify-self: center;
    width: 200px;
}

.product-title {
    font-size: 20px;
    font-weight: bold;
}

.product-item p {
    margin-top: 5px;
    margin-bottom: 5px;
}

.product-price {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: max-content max-content;
}

.price {
    grid-row: 1 / 2;
    grid-column: 1 / 2;

    color: var(--grays_low);
    font-weight: bold;
}

.product-price span {
    grid-row: 2 / 3;
    grid-column: 1 / 2;

    font-weight: bold;
    font-size: 25px;
}

.add-basket-plus {
    align-self: center;
    grid-row: 1 / 3;
    grid-column: 2 / 3;
    aspect-ratio: 1 / 1;
    max-height: 45px;
    margin-right: 7px;

    justify-self: end;
    background-color: var(--whites);

    border: 2px solid var(--blues);
    border-radius: 10px;

    padding: 7px;
    padding-bottom: 4px;
    cursor: pointer;
}

.add-basket-plus img {
    width: 25px;
    filter: invert(50%) sepia(100%) saturate(10000%) hue-rotate(240deg);
}

.add-basket-check {
    grid-row: 1 / 3;
    grid-column: 2 / 3;
    aspect-ratio: 1 / 1;
    max-width: 60px;
    
    justify-self: end;
    background-color: var(--blues);

    border: none;
    border-radius: 10px;

    padding: 20px;
    padding-bottom: 16px;
    cursor: pointer;
}

.add-basket-check img {
    width: 20px;
    filter: invert(100%); 
}
