:root {
    --blues: #001affee;
    --greys_high: #616161;
    --greys_mid: #9f9f9ff1;
    --grays_low: #aca9a9;
    --whites: white;
    --grays: gray;
    --shadows: #00000080;
    --bg-color: rgba(0, 0, 0, 0.404);
  }

.basket-item {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    border: 1px solid var(--greys_mid);
    border-radius: 15px;
    padding: 15px;
    margin-bottom: 30px;
}

.basket-img {
    width: 70px;
}

.close-btn {
    width: fit-content;
    justify-self: end;
    align-self: center;
    padding: 10px 12px 10px 12px;
    background-color: var(--whites);
    color: var(--greys_mid);
    border: 1px solid var(--grays);
    border-radius: 12px;
    cursor: pointer;
}