:root {
    --blues: #001affee;
    --greys_high: #616161;
    --greys_mid: #9f9f9ff1;
    --grays_low: #aca9a9;
    --whites: white;
    --grays: gray;
    --shadows: #00000080;
    --bg-color: rgba(0, 0, 0, 0.404);
    --blacks: black;
  }

.banner-section {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
}

.banner {
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    justify-items: end;
    align-items: center;

    margin-left: 10px;
    margin-right: 10px;

    background-image: url("/public/img/banner.jpeg");
    background-size: cover;
    background-position: center;
    border-radius: 30px;
}

.text-banner {
    color: var(--blacks);
    font-size: 40px;
    font-weight: bold;
    margin-left: 50px;
}

.text-banner span {
    color: var(--blues);
}

.banner-btn {
    background-color: var(--blues);
    color:var(--whites);

    margin-top: 20px;
    padding: 20px;
    padding-left: 25px;
    padding-right: 25px;

    font-size: 20px;
    font-weight: bold;

    border-radius: 20px;
    border: none;

    transition: box-shadow 0.2s ease-out;
    cursor: pointer;
}

.banner-btn:hover {
    box-shadow: 5px 5px 5px 0px var(--blacks);
}

.banner-btn:active {
    background-color:var(--whites);
    color: var(--blues);
}