:root {
  --blues: #001affee;
  --greys_high: #616161;
  --greys_mid: #9f9f9ff1;
  --grays_low: #aca9a9;
  --whites: white;
  --grays: gray;
  --shadows: #00000080;
  --bg-color: rgba(0, 0, 0, 0.404);
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-color);
  z-index: 1;
}

.basket {
  display: grid;
  grid-template-rows: 0.5fr 6fr 1fr;
  position: absolute;
  width: 700px;
  right: 0;
  height: 100%;
  background-color: var(--whites);
  box-shadow: -10px 0px 20px var(--shadows);
  padding: 0px 50px 0px 50px;
}

.title-block {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.title-block h2 {
  font-size: 32px;
}

.close-btn {
  width: fit-content;
  justify-self: end;
  align-self: center;
  padding: 10px 12px 10px 12px;
  background-color: var(--whites);
  color: var(--greys_mid);
  border: 1px solid var(--grays);
  border-radius: 12px;
  cursor: pointer;
}

.basket-list {
  height: auto;;
  overflow: auto;
}

.total-price {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 2fr;
}

.total-price button {
  grid-row: 2/3;
  grid-column: 1/3;
  height: fit-content;
  background-color: var(--blues);
  border-radius: 15px;
  color: var(--whites);
  font-size: 22px;
  padding: 20px;
  align-self: center;
  cursor: pointer;
}

.total-price button:active {
  background-color: var(--whites);
  color: var(--blues);
  border: 2px solid var(--blues);
}

.total-price p {
  font-size: 20px;
  font-weight: bold;
}

.total-price-summ {
  justify-self: end;
}